@keyframes showAndHide {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

#imageSequence {
  padding: 0;
  margin: 0;
  height: 100%;
  background-color: #000;

  image {
    padding: 0;
    margin: 0;
    animation-name: showAndHide;
    animation-duration: 5s;
    animation-delay: 0s;
    animation-timing-function: linear;
    animation-fill-mode: forwards;
  }
}